import React, { Component } from 'react';
import Fetch from 'react-fetch-component';

export default class ListPadel extends Component {
  render() {
    const url = `/api/padel`;

    return (
      <div>
        <h1>Padel</h1>
        <Fetch url={url} >
          {({ loading, data, error }) => (
            <div>
              {loading && <div class="spinner-border" role="status">
                <span class="visually-hidden"></span>
              </div>}
              {data && data.map((val, i) => {
                return <div class="card text-center">
                  <div class="card-header">
                    <span class="badge bg-primary">{val.date}</span>
                    <span class="small text-muted p-3">{val.weekday}</span>
                  </div>
                  <div class="card-body">
                    <h5 class="card-title">{val.name}</h5>
                    <p class="card-text">{val.address}</p>
                  </div>
                  <div class="card-footer text-muted small">{val.duration + ' -- ' + val.cost}</div>
                </div>
              })}
            </div>
          )}
        </Fetch>
      </div>
    );
  }
}